
* {
  margin: 0;
  padding: 0;
}
html {
  overflow-x: hidden;
  background: #e7e7e7;
}
body {
  display: -ms-flexbox;
  display: flex;
  overflow-x: hidden;
}
#app {
  font-family: '微软雅黑', Source Sans Pro, Helvetica, sans-serif;
  width: 100%;
}
#app a {
  color: #42b983;
  text-decoration: none;
}
#play-bar {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  background: -webkit-linear-gradient(top, #f9f9f9, #f3f3f3);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.play-bar-image-container {
  width: 37.5px;
  height: 37.5px;
  padding-left: 15px;
  cursor: pointer;
}
.play-bar-image {
  width: 37.5px;
  height: 37.5px;
  border-radius: 5px;
  display: inline-block;
}
.play-bar-text {
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding-left: 10px;
  cursor: pointer;
}
.play-bar-button {
  width: 20px;
  height: 20px;
  padding-right: 15px;
  cursor: pointer;
}
.fade-enter-active {
  transition: all .3s ease;
}
.fade-leave-active {
  transition: all .3s ease-out;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.play-slide-enter-active {
  transition: all .3s ease;
}
.play-slide-leave-active {
  transition: all .3s ease-out;
}
.play-slide-enter, .play-slide-leave-active {
  margin-top: 100vh;
}
.bar-slide-enter-active {
  transition: all .3s ease;
}
.bar-slide-leave-active {
  transition: all .3s ease-out;
}
.bar-slide-enter, .bar-slide-leave-active {
  margin-bottom: -50px;
}
@media screen and (min-width: 450px) {
body {
    width: 450px;
    margin: 0 auto;
}
#play-bar {
    width: 450px;
}
}





