/*border-1px 部分*/
.border-1px[data-v-4] {
  position: relative;
}
.border-1px-after[data-v-4]:after {
  border-top: 1px solid #d0d0d0;
  content: ' ';
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
}
.border-1px-before[data-v-4]:before {
  border-top: 1px solid #d0d0d0;
  content: ' ';
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
}
.border-1px[data-v-4]:before {
  top: 0;
}
.border-1px[data-v-4]:after {
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px[data-v-4]:after, .border-1px[data-v-4]:before {
    -webkit-transform: scaleY(.7);
    -webkit-transform-origin: 0 0;
    transform: scaleY(.7);
}
.border-1px[data-v-4]:after {
    -webkit-transform-origin: left bottom;
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px[data-v-4]:after, .border-1px[data-v-4]:before {
    transform: scaleY(.5);
}
}



